<template>
  <div>
    <div class="container">
      <div class="columns is-multiline">
        <div class="column">
          <article class="section">
            <div class="title">
              <p># Streaming Online</p>
            </div>
              <div class="columns is-multiline is-mobile is-variable is-1-mobile">
                <div v-if="data === null" class="column has-text-centered">
                  <p class="is-size-3">Loading...</p>
                </div>
                <div class="column is-three-quarters-tablet is-full-mobile" v-if="data !== null">
                  <p class="is-size-4"><b>{{data.title}}</b></p>
                  <p>Episode {{data.episode.id}}</p>
                  <br>
                  <div v-html="data.episode.link">
                  </div>
                  <br>
                  <button class="button is-primary">Download (GDrive)</button>
                  <button class="button is-primary">Download (Zippyshare)</button>
                  <button class="button is-primary">Download (Firebase)</button>
                </div>

                <div class="column episode-list" v-if="data !== null">
                  <article class="message">
                    <div class="message-header">
                      <p>Anime Info</p>
                    </div>
                    <div class="message-body">
                      <p>Category: Winter 2019</p>
                      <p>Genre: Romance</p>
                    </div>
                  </article>
                  <aside class="menu">
                    <p class="menu-label">
                      Daftar Episode
                    </p>
                    <ul class="menu-list">
                      <li><a>Episode 1</a></li>
                      <li><a>Episode 2</a></li>
                    </ul>
                  </aside>
                </div>
              </div>
          </article>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from '@/router/index'

export default {
  name: 'Homepage',
  data () {
    return {
      data: null
    }
  },
  methods: {
  },
  created () {
    db.collection('anime').where('slug', '==', this.$route.params.slug).get().then((snapshot) => {
      snapshot.forEach((doc) => {
        let data = {}
        data = doc.data()
        db.collection('anime/' + this.$route.params.slug + '/episodes').where('id', '==', this.$route.params.episode).get().then(snapshot => {
          snapshot.forEach(eps => {
            data.episode = eps.data()
            this.data = data
          })
        })
      })
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .video-responsive{
      overflow:hidden;
      padding-bottom:56.25%;
      position:relative;
      height:0;
  }
  .video-responsive iframe{
      left:0;
      top:0;
      height:100%;
      width:100%;
      position:absolute;
  }
  .episode-list{
    overflow-y: scroll;
  }
</style>



